import {
    Button,
    Layout,
    Page,
    Form,
    TextField
} from '@shopify/polaris';


class Login extends React.Component {
    render() {
        return (
            <Page>
                <Layout>
                    <Form>
                        <TextField
                            value={username}
                            onChange={handleEmailChange}
                            label="User Name"
                            type="text"
                            helpText={
                                <span>
                                    Weâ€™ll use this email address to inform you on future changes to
                                    Polaris.
                                </span>
                            }
                        />
                    </Form>
                </Layout>
            </Page>
        );
    }
}

export default Login;